export const groupNamePattern = /^[a-zA-Z0-9][a-zA-Z0-9-_ ]*[a-zA-Z0-9]$/
export interface Group {
  ancestor?: string[];
  type?: string;
  name: string;
  parent?: string;
  groupId?: string;
  facilityId?: string;
  facility_id?:string;
  isShow?: boolean;
  expanded?: boolean;
  hasChildren?: boolean;
  children?: string[];
  id?: string;
  tempName?: string;
  parentCard?: boolean
}

export interface AssignGroup {
  patientId: string,
  cGroup: GroupObj,
  pGroup: GroupObj
}

export interface GroupObj {
  id: string,
  name: string
}

export enum GroupType {
  CLINICAL = "clinical",
  PHYSICAL = "physical"
}

export interface GroupResponse {
  name: string,
  groupId: string,
  FacilityId: string,
  type: GroupType,
  alertId: string,
  alertDestId: string,
  miscSettingsId: string,
  children: string[],
  parent: string,
  ancestor: string[],
  AlertIsInherited: boolean,
  isShow?: boolean;
  expanded?: boolean;
  deleted?: boolean;
}
export const restrictedGroupNames = [
   "ROOT",
   "UNASSIGNED",
   "ALL"
]
